<template>
    <header>
        <Logo></Logo>
        
        <ul v-if="!isMobile" class="menu">
            <li v-for="menu in menuArray" :key="menu.pageName">
                <a :href="menu.pageLink">{{ menu.pageName }}</a>
            </li>
        </ul>
        
        <ul v-if="isMobile" :class="{open: isOpen}" class="mobile">
            <li v-for="menu in menuArray" :key="menu.pageName">
                <a :href="menu.pageLink">{{ menu.pageName }}</a>
            </li>
        </ul>
        
        <div class="header-icons">
            <fa :icon="[ 'far','user' ]"></fa>
            <fa icon="shopping-cart"></fa>
            <fa icon="bars" @click="isOpen = !isOpen"></fa>
        </div>
    </header>
</template>

<script>
import Logo from '../components/Logo'

export default {
    components: {
        Logo
    },
    data() {
        return {
            isMobile: false,
            isOpen: false,
            menuArray: [
                {
                    pageName: 'Home',
                    pageLink: 'home'
                },
                {
                    pageName: 'Products',
                    pageLink: 'products'
                },
                {
                    pageName: 'About us',
                    pageLink: 'about'
                },
                {
                    pageName: 'Contact us',
                    pageLink: 'contact'
                }
            ]
        }
    },
    methods: {
        screenWidth() {
            window.innerWidth <= 768 ? this.isMobile = true : this.isMobile = false
            window.innerWidth >= 768 ? this.isOpen = false : false
        }
    },
    mounted() {
        this.screenWidth()
        window.addEventListener('resize', () => this.screenWidth())
    },
}
</script>

<style lang="scss" scoped>
    header {
        display: flex;
        justify-content: space-between;
        
        .menu {
            display: flex;
            justify-content: space-between;
            
            li {
                padding: 0 20px;
                a {
                    font-size: 16px;
                    color: #222;
                    line-height: 20px;
                }
            }
        }
        
        .mobile {
            display: flex;
            flex-direction: column;
            position: fixed;
            left: 0;
            margin-top: 30px;
            padding: 20px;
            background: #F9F6F5;
            transition: 0.5s;
            transform: translateX(-100%);
            z-index: 999;
            
            
            &.open {
                transform: translateX(0);
            }
             
            li {
                a {
                    font-size: 16px;
                    color: #222;
                    line-height: 20px;
                }
            }
        }
    
        .header-icons {
            display: flex;
            justify-content: space-between;
            
            svg {
                margin-left: 24px;
            }
        }
    }
</style>